@import '../../components/variables/variables.scss';
.appbar {
  position: fixed;
  top: 0;
  min-height: 75px;
  width: 100%;
  display: flex;
  align-items: center;
  background-color: rgba($color: $purple-light, $alpha: 0.8);
  backdrop-filter: saturate(80%) blur(20px);
  z-index: 2;
}

.appbar_wrapper {
  display: flex;
  justify-content: space-between;
  align-items: center;
  width: 100%;
  padding: 15px;
}

.appbar_logo {
  display: flex;
  align-items: center;
  img {
    width: 15px;
  }
  p {
    text-transform: uppercase;
    letter-spacing: 4px;
    color: $pink;
    margin-left: 12px;
  }
}

.appbar_avatar {
  img {
    width: 50px;
    height: 50px;
    display: none;
  }
  svg {
    color: $purple-lighter;
    font-size: 1.5rem;
    margin-left: 10px;
    cursor: pointer;
  }
}
@include for-desktop {
  .appbar_avatar {
    img {
      display: initial;
    }
    svg { 
      display: none;
    }
  }
} 